@import url(https://fonts.googleapis.com/css2?family=Chango&display=swap);
body {
margin : 1em auto;
padding: 0;
box-sizing: border-box;
font-family: 'Courier New', Courier, monospace;
background-color: #F9EEF1;
}

.app {
  display : flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: column;
  margin: auto;
}
.header{background-color:#f5c0c0;padding:1em;text-align:center}
.player{text-align:center;box-shadow:5px 5px 15px rgba(0,0,0,.4);width:400px;height:auto;border-radius:10px}.player__title{text-transform:uppercase}.player__details{flex-wrap:wrap}.player__details__img{overflow:hidden;border-radius:30%;width:250px;margin:auto}.player__details__image{height:auto;width:100%}.player__controls{display:flex;align-items:center;justify-content:center;margin-bottom:1em}.player .playBtn{margin:0;padding:20px;border-radius:50%;background-color:#f5c0c0;border:none;outline:none;cursor:pointer}.player .skipBtn{background:none;margin:0;padding:20px;border:none;outline:none;cursor:pointer;color:#888}
